<template>
	<view class="body">
		<view class="words">
			<view v-for="(item,index) in data" :key="index" class="express_content">
				<view>
					<view class="h5">{{item.word}}</view>
					<view class="h6">{{item.explain}}</view>
				</view>
				<u-icon v-if="item.is_collection==false" name="star-fill" color="#e3e3e3" size="48rpx" @click="isStar(item)"></u-icon>
				<u-icon v-else name="star-fill" color="#ffd75c" size="48rpx" @click="isStar(item)"></u-icon>
			</view>
		</view>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import { wordBook, setCollection } from '@/common/http.api.js';
	export default {
		data() {
			return {
				data:{}
			}
		},
		onLoad() {
			let token = store.state.vuex_token 
			let id = store.state.subject_id 
			wordBook({subject_id:id,token}).then(res=>{
				console.log(res)
				this.data = res.data.data
			}).catch(err=>{
				console.log(err)
			})
		},
		methods:{
			// 点击收藏
			isStar(item) {
				let token = store.state.vuex_token 
				let id = store.state.subject_id 
				console.log(item)
				setCollection({token, rare_word_id:item.id}).then(res=>{
					console.log(res)
					wordBook({subject_id:id,token}).then(res=>{
						console.log(res)
						this.data = res.data.data
					}).catch(err=>{
						console.log(err)
					})
				}).catch(err=>{
					console.log(err)
				})
			},
		}
	}
</script>

<style lang="scss">
	.body{
		padding: 30rpx;
		.express_content{
			display: flex;
			justify-content: space-between;
			margin: 20rpx 0;
		}
		.h5 {
			font-size: 32rpx;
			font-weight: 500;
			line-height: 48rpx;
		}
		.h6 {
			font-size: 24rpx;
			line-height: 36rpx;
			color: #999;
			font-weight: 400;
			margin-bottom: 20rpx;
		}
	}
</style>